import * as React from 'react';
import { useDispatch } from 'react-redux';
import { useState } from 'react';
import { login } from '../../redux/auth/authOperations';
import useAuth from '../../hooks/useAuth';
import { FiEye, FiEyeOff } from 'react-icons/fi';
import toast from 'react-hot-toast';

import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import { createTheme, ThemeProvider } from '@mui/material/styles';

function Copyright() {
  return (
    <Typography variant="body2" color="text.secondary" align="center">
      {'Copyright Â© '}
      <Link
        color="inherit"
        href="https://drimak-stepan.github.io/goit-react-hw-08-phonebook/"
      >
        Your Website
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}
const theme = createTheme();

export const LoginForm = () => {
  const [showPassword, setShowPassword] = useState(false);
  const { isError } = useAuth();

  const dispatch = useDispatch();

  const handleChange = () => {
    if (showPassword) {
      return setShowPassword(false);
    }
    setShowPassword(true);
  };

  const handleSubmit = e => {
    e.preventDefault();

    const form = e.currentTarget;
    dispatch(
      login({
        email: form.elements.email.value,
        password: form.elements.password.value,
      })
    );
    if (isError.status === 400) {
      return toast(`User not fount, please enter other data ...`, {
        style: { color: '#1976d2' },
      });
    }
    form.reset();
  };

  return (
    <ThemeProvider theme={theme}>
      <Grid container component="main" sx={{ height: '100vh' }}>
        <CssBaseline />
        <Grid
          item
          xs={false}
          sm={4}
          md={7}
          sx={{
            backgroundImage: 'url(https://source.unsplash.com/random)',
            backgroundRepeat: 'no-repeat',
            backgroundColor: t =>
              t.palette.mode === 'light'
                ? t.palette.grey[50]
                : t.palette.grey[900],
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            borderTopLeftRadius: 24,
            borderBottomLeftRadius: 24,
            marginBottom: 5,
          }}
        />
        <Grid
          item
          xs={12}
          sx={{
            marginBottom: 5,
            borderTopRightRadius: 24,
            borderBottomRightRadius: 24,
          }}
          sm={8}
          md={5}
          component={Paper}
          elevation={6}
          square
        >
          <Box
            sx={{
              my: 8,
              mx: 4,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
            }}
          >
            <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}></Avatar>
            <Typography component="h1" variant="h5">
              Sign in
            </Typography>
            <Box
              component="form"
              noValidate
              position="relative"
              onSubmit={handleSubmit}
              sx={{ mt: 1 }}
            >
              <TextField
                margin="normal"
                required
                fullWidth
                id="email"
                label="Email Address"
                name="email"
                autoComplete="email"
                autoFocus
              />

              <TextField
                margin="normal"
                required
                fullWidth
                name="password"
                label="Password"
                type={!showPassword ? 'password' : 'text'}
                id="password"
                autoComplete="current-password"
              />
              {!showPassword ? (
                <FiEyeOff
                  style={{
                    position: 'absolute',
                    top: 117,
                    right: 15,
                  }}
                  onClick={handleChange}
                />
              ) : (
                <FiEye
                  style={{ position: 'absolute', top: 117, right: 15 }}
                  onClick={handleChange}
                />
              )}

              <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2 }}
              >
                Sign In
              </Button>
              <Copyright sx={{ mt: 5 }} />
            </Box>
          </Box>
        </Grid>
      </Grid>
    </ThemeProvider>
  );
};
